import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

// Stats Icon
import statAntiAir     from "../../assets/AL/AntiAir.png";
// import statArmor       from "../../assets/AL/Armor.png";
import statASW         from "../../assets/AL/ASW.png";
import statAviation    from "../../assets/AL/Aviation.png";
// import statConsumption from "../../assets/AL/Consumption.png";
import statEvasion     from "../../assets/AL/Evasion.png";
import statFirepower   from "../../assets/AL/Firepower.png";
import statHealth      from "../../assets/AL/Health.png";
import statHit         from "../../assets/AL/Hit.png";
import statLuck        from "../../assets/AL/Luck.png";
import statReload      from "../../assets/AL/Reload.png";
import statTorpedo     from "../../assets/AL/Torpedo.png";

const propTypes = { stats: PropTypes.object };

export default class Stats extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      stats: {
        health: 0,
        firepower: 0,
        aviation: 0,
        torpedo: 0,
        evasion: 0,
        antiair: 0,
        reload: 0,
        luck: 0,
        hit: 0,
        asw: 0
      }
    };
  };

  componentDidUpdate(prevProps, prevState)
  {
    if (prevState.stats !== this.props.stats)
      this.setState({ stats: this.props.stats });
  };

  render()
  {
    const statsDOM = (image, value) => (
      <Table className={value === 0 ? "width-inherit bs-1 d-none" : "width-inherit bs-1"}>
        <thead>
          <tr>
            <th className="ratio-50px">
              <img src={image} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
            </th>
            <th className="ratio-50px">{value}</th>
          </tr>
        </thead>
      </Table>
    );
    return (
      <div className="containerCS">
        {statsDOM(statHealth, this.state.stats.health)}
        {statsDOM(statReload, this.state.stats.reload)}
        {statsDOM(statEvasion, this.state.stats.evasion)}
        {statsDOM(statHit, this.state.stats.hit)}
        {statsDOM(statLuck, this.state.stats.luck)}
        {statsDOM(statFirepower, this.state.stats.firepower)}
        {statsDOM(statAntiAir, this.state.stats.antiair)}
        {statsDOM(statTorpedo, this.state.stats.torpedo)}
        {statsDOM(statAviation, this.state.stats.aviation)}
        {statsDOM(statASW, this.state.stats.asw)}
      </div>
    );
  };
};

Stats.propTypes = propTypes;
